import { Injectable } from '@angular/core';
import { map, catchError, tap } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DataTransferService {

  constructor(private http: HttpClient) { }
  baseURL = 'https://transfer.airliftnet.com/api/';

  getRateData(body): Observable<{}> {
    return this.http.post(this.baseURL + `getHblNumber/`, body )
    .pipe( map(this.extractData), catchError(this.handleError));
  }
  postCrmtoTracking(body): Observable<{}> {
    return this.http.post(this.baseURL + `crmtotracking/`, body )
    .pipe( map(this.extractData), catchError(this.handleError));
  }
  private handleError(error): any {
    return throwError(error);
  }
  private extractData(res: Response): any {
    const body = res;
    return body || {};
  }

}
