  subValueChanges(item, value) {
    let values = value;
    if (item.field_sub_type == "date") {
      let d = new Date(value),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();
      if (month.length < 2)
        month = '0' + month;
      if (day.length < 2)
        day = '0' + day;
      values = [year, month, day].join('-');
    }

    let temp = { "id": this.SubIndex, [item.name]: values }
    this.CommonService.commonPut(temp, this.subTableTemp.table_name).subscribe((res: any) => {
      if (res.statusCode == 200) {
        this.subValuesRow[item.name] = values;
      }
    });
  }

  insideSubValueChangeAll(ind, parentTable, item, value, id) {
    // console.log("sssssssss", item, ind, parentTable, item, value, id)
    let values = value;
    if (item.field_sub_type == "date") {
      let d = new Date(value),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();
      if (month.length < 2)
        month = '0' + month;
      if (day.length < 2)
        day = '0' + day;
      values = [year, month, day].join('-');
    }

    let temp = { "id": id, [item.name]: values }
    this.CommonService.commonPut(temp, parentTable).subscribe((res: any) => {
      if (res.statusCode == 200) {
        this.insidesubValueInfo[parentTable][ind][item.name] = values;
      }
    });
  }

