import { DataTransferService } from './data-transfer.service';
import { Component, OnInit } from '@angular/core';
import { CommonService } from '../common.service';

@Component({
  selector: 'app-data-transfer',
  templateUrl: './data-transfer.component.html',
  styleUrls: ['./data-transfer.component.css']
})
export class DataTransferComponent implements OnInit {
  hblList: any = [];
  value: any = {
    id : '',
    hbl_number : ''
  };
  user = JSON.parse(localStorage.getItem('user_data'));
  loader = false;
  constructor(private dataTransferService : DataTransferService, private CommonService: CommonService) { }

  ngOnInit(): void {
    // this.getData('CHN5');
  }
  getData(val): any {
    console.log('HERE');
    const res = {
      hbl : val
    }
    this.dataTransferService.getRateData(res).subscribe((resp) => {
      this.hblList = resp;
    });
  }
  onChange(val): any {
    console.log(val)
    this.value = val;
  }
  focus(): any {}

  transferData(): void {
    this.loader = true;
    console.log(this.user);
    if (!!this.user) {
      this.value.user_id = this.user.id;
    }
    setTimeout(() => {
      this.dataTransferService.postCrmtoTracking(this.value).subscribe((resp:any) => {
        this.loader  = false;
        if (resp.code == 200) {
          this.CommonService.showSnackbar('Data Transfered successfully', ['green-growl']);
        } else {
          this.CommonService.showSnackbarUnhide('Failure,Data not Transfered, Please Try Again or Contact Admin', ['red-growl']);
        }
      }, (error: any) => {
        this.CommonService.showSnackbarUnhide('Failure,Data not Transfered, Please Try Again or Contact Admin', ['red-growl']);
      });
      }, 10);
  }
}
